header {
    position: relative;
    .burger {
        display: none;
        &:hover {} }

    .navToolBar {
        display: flex;
        justify-content: space-between;
        &Icons {
            display: flex; }
        &IconContainer {
            padding: 5px; } } }
nav {
    ul {
        li {
            padding: 10px; }
        .navOption {
            text-decoration: none;
            font-size: 16px;
            cursor: pointer; } } }

.desktopNav {
    ul {
        display: flex; } }



@media(max-width: $mobile-break) {
    header {
        .burger {
            display: block; }
        .desktopNav {
            display: none; } } }
